.App {
  text-align: center;
}

#content {
  height: 400px;
}

#header-text {
  font-size: 25px;
}

#current-verb {
  font-size: 25px;
  color: #007bff;
}

.verb-icon {
    cursor: pointer;
    color: #007bff;
}

.verb-form {
  height: 30px;
  margin: 5px;
  padding: 5px;
}

.verb-elem-text {
    font-size: 15px;
}

.verb-elem-answer {
    color: #007bff;
}

.verb-elem-type {
    min-width: 110px;
}

.verb-variant-item {
    margin: 2px;
}

#verb-card {
  min-height: 300px;
  min-width: 310px;
}

#verb-list {
    margin-top: -20px;
}

@media (max-width: 540px) {
  #header-text {
      font-size: 20px;
  }
  .verb-elem-text {
      font-size: 13px;
  }
}
